import Avatar from "@components/avatar"
import { Card, CardHeader, CardTitle, CardText, Col } from "reactstrap"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import { getDataList } from "@src/shared/dynamic_pages/store"
import { useEffect, useContext } from "react"
import illustration from "@src/assets/images/illustration/email.svg"

const WidgetUsersTopEntry = ({ storeName, endpointName }) => {
  const { t } = useTranslation()
  const store = useSelector((state) => state.dynamicStore)
  const dispatch = useDispatch()

  useEffect(() => {
    if (
      !store.pageData[storeName] ||
      (store.pageData[storeName]?.data?.length === 0 &&
        !store.pageData[storeName]?.isLoading)
    ) {
      dispatch(
        getDataList({
          storeName: storeName,
          endpointName: endpointName
        })
      )
    }
  }, [])

  return (
    store.pageData[storeName]?.data?.length > 4 && (
      <Col>
        <Card className="card-browser-states">
          <div
            style={{
              backgroundColor: "rgba(115, 103, 240, .1)"
            }}
            className="meetup-img-wrapper rounded-top text-center"
          >
            <img
              style={{
                marginTop: "38px"
              }}
              src={illustration}
              height="170"
            />
          </div>
          <CardHeader
            style={{
              position: "absolute",
              width: "100%"
              // , padding: "10px"
            }}
            className="justify-content-around"
          >
            <div className="text-center">
              <CardTitle styles={{ fontSize: "1.4rem" }} tag="h3">
                ðŸŽ‰ {t("Si Paling Aktif")}!
              </CardTitle>
              <CardText className="font-small-2">
                {t(
                  "Ranking 10 besar jumlah aktifitas user selama 1 bulan terakhir."
                )}
              </CardText>
            </div>
            {/* <UncontrolledDropdown className="chart-dropdown">
          <DropdownToggle
            color=""
            className="bg-transparent btn-sm border-0 p-50"
          >
            <MoreVertical size={18} className="cursor-pointer" />
          </DropdownToggle>
          <DropdownMenu end>
            <DropdownItem className="w-100">Last 28 Days</DropdownItem>
            <DropdownItem className="w-100">Last Month</DropdownItem>
            <DropdownItem className="w-100">Last Year</DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown> */}
          </CardHeader>
          <div className="leader-board-widget">
            {/* {renderStates()} */}

            <div class="ribbon"></div>
            <table>
              {store.pageData[storeName]?.data?.map((item, index) => (
                <tr>
                  <td style={{ paddingRight: "0.5rem" }} class="number">
                    {index + 1}
                  </td>
                  <td style={{ paddingLeft: "0.5rem" }} class="name">
                    <div className="d-flex">
                      <Avatar
                        className="me-50"
                        img={item?.avatar}
                        imgHeight={37}
                        style={{ width: "37px" }}
                        contentStyles={{ width: "37px", height: "37px" }}
                        imgWidth={37}
                        content={item?.name}
                        initials={!item.avatar}
                        randomColorLight={true}
                      />

                      <h6
                        style={{
                          color: "inherit",
                          fontSize: "inherit",
                          fontWeight: "inherit"
                        }}
                        className="align-self-center mb-0"
                      >
                        {item?.name}
                      </h6>
                    </div>
                  </td>
                  <td style={{ lineHeight: "normal" }} class="points">
                    {item?.count ?? ""}

                    {index === 0 && (
                      <img
                        class="gold-medal"
                        src="https://github.com/malunaridev/Challenges-iCodeThis/blob/master/4-leaderboard/assets/gold-medal.png?raw=true"
                        alt="gold medal"
                      />
                    )}
                  </td>
                </tr>
              ))}
            </table>
          </div>
        </Card>
      </Col>
    )
  )
}

export default WidgetUsersTopEntry
